<template>
  <div class="single-blank-config">
    <Form :label-width="120">
      <div class="config"
           v-for="(item, index) in currentValue" :key="index">
        <!-- 代码说明 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'note'">
          <Input type="textarea" v-model="item.configData.configData"></Input>
          <!-- {{ item.configData }} -->
        </Form-item>
        <!-- 小报告 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'report'">
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" />
            </Col>
          </Row>
        </Form-item>
      </div>
    </Form>
  </div>
</template>

<script>
import Editor from '@/components/editor'

export default {
  name: 'report',
  components: {
    Editor
  },
  props: {
    cType: {
      type: Number,
      require: true
    },
    configs: {
      type: Array,
      require: true
    }
  },
  watch: {
    currentValue (newValue, oldValue) {
      this.$emit('input', {
        value: this.currentValue
      })
    }
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-0000000000024',
      currentValue: null
    }
  },
  created () {
    this.currentValue = this.deepCopy(this.configs)
  }
}
</script>
